import { logLevel } from 'kafkajs';
import { Logger } from '@transai/logger';

export const TransAILogCreator = () => {
  return ({ level, log }: { level: logLevel; log: { message: string } }) => {
    const logger = Logger.getInstance();
    const { message, error } = log as { message: string; error?: string };
    const parsedMessage = error ? `${message} - ${error}` : message;
    switch (level) {
      case logLevel.ERROR:
      case logLevel.NOTHING:
        logger.error(parsedMessage);
        break;
      case logLevel.WARN:
        logger.warn(parsedMessage);
        break;
      case logLevel.INFO:
        logger.info(parsedMessage);
        break;
      case logLevel.DEBUG:
        logger.debug(parsedMessage);
        break;
      default:
        logger.info(parsedMessage);
        break;
    }
  };
};
